// src/components/ui/FirebaseUIWidget.tsx
"use client";
import React, { useEffect, useRef, useState } from "react";
import * as firebaseui from "firebaseui"; // Changed import here!
import { auth } from "@/firebase/config";
import "firebase/compat/auth";
import "firebaseui/dist/firebaseui.css";

interface FirebaseUIWidgetProps {
  uiConfig: firebaseui.auth.Config;
}

const FirebaseUIWidget: React.FC<FirebaseUIWidgetProps> = ({ uiConfig }) => {
  const firebaseUiContainer = useRef<HTMLDivElement | null>(null);
  const [ui, setUi] = useState<firebaseui.auth.AuthUI | null>(null); // Add state to hold AuthUI instance

  useEffect(() => {
    console.log("FirebaseUIWidget: useEffect - Initializing"); // Debugging
    if (firebaseUiContainer.current) {
      try {
        // Check if an AuthUI instance already exists
        const existingUi =
          firebaseui.auth.AuthUI.getInstance() ||
          new firebaseui.auth.AuthUI(auth);

        existingUi.start(firebaseUiContainer.current, uiConfig);
        setUi(existingUi); // Store the AuthUI instance in state
      } catch (error) {
        console.error("FirebaseUI Error:", error);
      }
    }

    // Cleanup function: Delete the AuthUI instance when the component unmounts or auth changes
    return () => {
      console.log("FirebaseUIWidget: useEffect - Cleaning up"); // Debugging
      // Check if an AuthUI instance exists before deleting
      if (ui) {
        ui.delete();
      }
    };
  }, [auth, uiConfig]); // Depend on the auth instance

  return <div ref={firebaseUiContainer} />;
};

export default FirebaseUIWidget;
